import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            list1: null,
            choose1: [],
            choose2: [],
            choose3: [],
            text1: '',
            text2: '',
            text3: '',
            patient: this.$store.state.patient
        }
    },
    created(){
        this.pull()
    },
    methods: {
        pull(){
            this.$post('PlatFormAPI/Category/QueryCategoryItem',{
                CategoryIds: [ 48 ]
            }).then(o => {
                o.forEach(v => {
                    if(v.CategoryId == 48){
                        this.list1 = v.Items
                    }
                })
                const info = this.$store.state.Health
                if(info && info.FamilyHealthHistorys){
                    info.FamilyHealthHistorys.forEach(v => {
                        if(v.Relation == 1){
                            this.text1 = v.HealthHistory
                        }
                        else if(v.Relation == 2){
                            this.text2 = v.HealthHistory
                        }
                        else if(v.Relation == 3){
                            this.text3 = v.HealthHistory
                        }
                    })
                }
            })
        },
        send(){
            this.$post('PlatFormAPI/PatientAccount/SaveFamilyHealthHistory',{
                PatientId: this.patient.PatientId,
                FamilyHealthHistorys: [
                    {
                        Relation: 1,
                        HealthHistory: this.text1
                    },
                    {
                        Relation: 2,
                        HealthHistory: this.text2
                    },
                    {
                        Relation: 3,
                        HealthHistory: this.text3
                    }
                ]
            }).then(o => {
            	this.$store.dispatch('pushIsFresh',true)
                this.$toast('保存成功')
                this.$router.back()
            })
        },
        push(i,id){
            if(id == 1){
                const idx = this.choose1.indexOf(i)
                if(idx == -1){
                    this.text1 = ''
                    this.choose1.push(i)
                    this.choose1.forEach(v => {
                        this.text1 += v.CategoryItemName+";"
                    })
                }
                else{
                    this.text1 = ''
                    this.choose1.splice(idx,1)
                    this.choose1.forEach(v => {
                        this.text1 += v.CategoryItemName+";"
                    })
                }
            }
            else if(id == 2){
                const idx = this.choose2.indexOf(i)
                if(idx == -1){
                    this.text2 = ''
                    this.choose2.push(i)
                    this.choose2.forEach(v => {
                        this.text2 += v.CategoryItemName+";"
                    })
                }
                else{
                    this.text2 = ''
                    this.choose2.splice(idx,1)
                    this.choose2.forEach(v => {
                        this.text2 += v.CategoryItemName+";"
                    })
                }
            }
            if(id == 3){
                const idx = this.choose3.indexOf(i)
                if(idx == -1){
                    this.text3 = ''
                    this.choose3.push(i)
                    this.choose3.forEach(v => {
                        this.text3 += v.CategoryItemName+";"
                    })
                }
                else{
                    this.text3 = ''
                    this.choose3.splice(idx,1)
                    this.choose3.forEach(v => {
                        this.text3 += v.CategoryItemName+";"
                    })
                }
            }
        }
    }
}



